import React from "react";
import Button from '@material-ui/core/Button';
import "./Card.css"

const SignUpCard =(props)=>{
    return (
        <div className="signup-card-container">
         <div className="signup-card-element inline-div">
                <img alt="signup-img" className="card-img" src="data:image/svg+xml;base64," />
            </div>
            <div className="signup-card-element inline-div">
                <div className="info">Sign up for a personal trainer to improve your results<Button style={{backgroundColor:"rgb(235, 161, 23)" ,height:"30px", marginTop:"15px",color:"white"}} variant="contained" ><p>Sign up</p></Button></div>
            </div>
        </div>
    )
}
export default SignUpCard;